.navigation-bar {
  position: fixed;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #faa936;
  padding: 1rem;
  border-radius: 5px 0 0 5px;
}

.navigation-bar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.navigation-bar li {
  margin-bottom: 1rem;
}

.navigation-bar a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

.navigation-bar a:hover {
  color: rgb(163, 163, 255);
}